.help {
    &__upper {
        padding: 0 0 20px;

        display: flex;
        align-items: center;
        justify-content: space-between;

        @media screen and (max-width: 600px) {
            flex-direction: column;
            align-items: flex-start;
            gap: 15px;
        }
    }

    &__button {
        width: 330px;

        @media screen and (max-width: 1025px) {
            width: 200px;
        }
    }

    &__container {
        display: grid;
        gap: 20px;

        font-size: 17px;
        line-height: 19px;
        font-weight: 400;
        color: $colorBlack;
        

        margin-top: 37px;

        span {
            display: block;
        }

        b {
            display: block;

            font-weight: 700;
        }
    }

    &__head {
        font-size: 27px;
        line-height: 29px;
        font-weight: 600;
        color: $colorBlack;

        display: block;
    }

    &__lists {
        margin-top: 20px;

        &-container {
            display: grid;
            gap: 15px;

            font-size: 17px;
            line-height: 25px;
            font-weight: 400;
            color: $colorBlack;

            margin-top: 10px;

            ul {
                list-style: disc;
                padding-left: 20px;
            }

            h4 {
                font-weight: 700;
            }
        }
    }

    &__bottom {
        display: grid;
        gap: 15px;

        font-family: $fontSource;
        font-size: 32px;
        line-height: 34px;
        font-weight: 400;
        color: $colorBlack;
        text-align: center;

        margin-top: 36px;

        @media screen and (max-width: 768px) {
            font-size: 21px;
            line-height: 23px;
        }

        span {
            display: block;
        }

        b {
            display: block;

            font-weight: 700;
        }
    }
}